import { h } from 'preact';
import Error404App from 'base/error404/error404App.js';

export default function Error404(props) {
	const {
		appProps,
		initialState = {},
		relativeRoot = '/',
		reload,
		type
	} = props;

	const {
		doc,
		imageRoot,
		title
	} = initialState;

	const buildDate = Date.now();

	return (
		<html lang="en">
			<head>
				<title>{title}</title>
				<meta http-equiv="Content-Type" content="text/html; charset=UTF-8" />
				<meta charset="UTF-8" />
				<meta name="author" content="POWSTER showtimes@powster.com" />
				<meta name="copyright" content={new Date().getFullYear()} />
				<meta name="viewport" content="minimal-ui, initial-scale=1" />
				<meta name="robots" content="noindex" />
				{doc.styleLinks && Object.keys(doc.styleLinks).map(link => {
					if (!doc.styleLinks[link] || typeof doc.styleLinks[link] !== 'string') return;
					return doc.styleLinks[link].split(',').map(url => {
						let href = url.trim().replace(/&amp;/g, '&');
						if (!href) {
							return;
						}
						if (href.startsWith('async:')) {
							href = href.slice(6);
							return <link rel="preload" href={href} as="style" onload="this.onload=null;this.rel='stylesheet'" />;
						}
						return <link rel="stylesheet" href={href} />;
					});
				})}
				<link rel="shortcut icon" type="image/png" href={`${imageRoot}/favico.png`} />
				<link rel="stylesheet" type="text/css" href={`https://${relativeRoot}/css/error404.css?v=${buildDate}`} />
			</head>
			<body>
				<div id="app">
					<Error404App {...appProps} />
				</div>
				{reload}
				{renderGlobalsScript(initialState, type, buildDate)}
				<script src={`https://${relativeRoot}/js/error404.js?v=${buildDate}`} charset="utf-8" />
			</body>
		</html>
	);

}

function renderGlobalsScript(initialState, type, buildDate) {
	const cleanInitialState = JSON.stringify(initialState).replace(/[\u2028\u2029]/g, '');
	const initialData = [
		`window.__INITIAL_STATE__ = ${cleanInitialState};`,
		`window.__BUILD_DATE__ = ${buildDate};`
	].join('\n');

	return (
		<script dangerouslySetInnerHTML={{ __html: initialData }} />
	);
}
